import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Shoulders({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
    <Helmet title={`Группа мышц - Плечи`} />
    <TopBanner style={shouldersStyle} title={'Упражнения для плеч'} />
      {posts
        .filter(post => post.node.frontmatter.filterby === 'shoulders')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const shouldersStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/ji0zvnl67/shoulders.jpg)',
	backgroundPosition: "bottom"
};

export const pageQuery = graphql`
  query ShouldersQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



